import { Children, Component, useState, Fragment, useEffect } from "react";
import { TextBars, Bar, Header, Button, Lists} from "../components/TextBars";
import "../Styles/global.css"
import "../Styles/ListOfMembers.css"
import arrow from "../components/arrow.png"
import axios from "axios";
import delayAdapterEnhancer from 'axios-delay';
import { useQuery } from "react-query";
import { toast } from "react-toastify";
import {Link} from 'react-router-dom';



const dat = [{
    name: ""
}]

export default class Login extends Component{

    constructor(props) {
        super(props);
        this.state = {
            datavalue: '',
            dat:''
        }
    }
    
    render() {



        const exit= () => {
            localStorage.removeItem('token')
            window.location.assign('/')
        }

        const clicked = (e) => {
            console.log('123')
            axios.get('http://79.174.92.124:9999/workouts/' + localStorage.getItem('token'), {
            })
            .then(response => {
                 this.setState({
                    datavalue: response.data
                    
                })
                response.data.forEach((entry) => {
                    this.setState({
                        dat: entry
                    })
                })    
            })
            .catch(error => {
                let errMsg = error.response ? `Серверная ошибка. Возможно не выполнен вход?` // Ответ сервера содержит ошибку
                                           : error.request ? 'Нет ответа' // Ответ от сервера не получен
                                                           : `Ошибка настройки запроса: ${error.message}`; // Неправильная настройка запроса
                toast.error(errMsg); // Подробности ошибки
              });}
        

        const dat = (this.state.datavalue)
        const listlen = (Object.keys(dat))

        return (<><div onMouseEnter={clicked}>
            <div style={{textAlign: 'center', marginTop: '20px'}}>
                <Link to='/choice' className="panel">Главная</Link>
                <Link to="/newMemb" className="panel">Новый участник</Link>
                <Link to="/ListOfMembers" className="panel">Список участников</Link>
                <Link className="panel" style={{color: 'red'}} onClick={exit}>Выйти</Link>
            </div>
            <button src={arrow} className="refresh" onClick={clicked}>
                <img className='refresh' src={arrow}/>
            </button>
            <div style={{textAlign: "center"}}>
                <Header title='Список участников'/>
            <table className="listes" style={{margin: "auto"}}>
                <thead>
                <tr style={{fontWeight: 'bolder', color:"white", background: "linear-gradient(to right, blue, rgb(255, 0, 251))", position: 'sticky', top:'0'}}>
                    <th>Фамилия</th>
                    <th>Имя</th>
                    <th>Отчество</th>
                    <th>Пол</th>
                    <th>Возраст</th>
                    <th>Подтягиваний</th>   
                </tr>
                </thead>
           {listlen.map((e)=>{ return(
            <tbody>
            <tr className="lststrng" style={{borderBottom: "1px solid gray"}}>
                <td>{dat[e].lastname}</td>
                <td>{dat[e].firstname}</td>
                <td>{dat[e].patronymic}</td>
                <td>{dat[e].name}</td>
                <td>{dat[e].old}</td>
                <td className="pullups">{dat[e].count_of_pull_up_great}</td>
            </tr>
            </tbody>)})}
           </table>
            </div>
            </div>
            </>)}}
