import { Component } from "react";
import { Bar, Header, Button } from "../components/TextBars";
import "../Styles/global.css"
import "../Styles/registration.css"
import logo from "../components/logo2.png"
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import { useActionData } from "react-router-dom";
import {Link} from 'react-router-dom';


export default class registration extends Component{

    constructor(props) {
        super(props);
        this.state = {
            datavalue: ''
        }
    }
    render() {

        const formSubmited = (e) => {
            e.preventDefault()
            const formData = new FormData(e.currentTarget);
            const data = Object.fromEntries(formData);
            console.log(data)
            this.setState({
                datavalue: data
            })
            const req = {
                "email": data.login,
                "firstname": data.firstName,
                "lastname": data.secondName,
                "password": data.pass1,
                "password_again": data.pass2
            }
            axios.post('http://79.174.92.124:9999/registration', req)
            .then( (e) => {
                localStorage.setItem('token', e.data.user_id)
                window.location.assign('/choice')
            })
            .catch(error => {
                let errMsg = error.response ? `Неверно введены данные` // Ответ сервера содержит ошибку
                                           : error.request ? 'Нет ответа' // Ответ от сервера не получен
                                                           : `Ошибка настройки запроса: ${error.message}`; // Неправильная настройка запроса
                toast.error(errMsg); // Подробности ошибки
              });
        };

        return(<>
            <div style={{textAlign: 'center', marginTop: '20px'}}>
                <p><img src={logo} className="imegst"/></p>
                <Link to='/' className="panel">Вход</Link><Link to="/registration" className="panel">Регистрация</Link>
            </div>
            <div className="reg">   
                <form onSubmit={formSubmited}>
                    <Header title="Регистрация"/>
                    <Bar type="text" for = 'loginreg' id='loginreg' title='Логин' placeholder="Почта" name='login'/>
                    <Bar type="text" for = 'firstName' id='firstName' title='Имя' name='firstName'/>
                    <Bar type="text" for = 'secondName' id='secondName' title='Фамилия' name='secondName' />
                    <Bar type="password" for = 'pass1' id='pass1' title='Пароль' name='pass1'/>
                    <Bar type="password" for = 'pass2' id='pass2' title='Пароль ещё раз' name='pass2'/>
                    <Button link="/" title = 'Зарегестрироваться'/>
                </form>
            </div>
            </>
        )
    }

    
}