import { Component, React } from "react";
import { TextBars, Bar, Header, Button } from "../components/TextBars";
import "../Styles/newMemb.css"
import "../Styles/global.css"
import { useForm } from "react-hook-form";
import { useMutation } from "react-query";
import axios from 'axios'
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer, toast } from 'react-toastify';
import {Link} from 'react-router-dom';


export default class Login extends Component{
    constructor(props) {
        super(props);
        this.state = {
            datavalue: ''
        }
    }
    
    render() {

        const exit= () => {
            localStorage.removeItem('token')
            window.location.assign('/')
        }

        const stopped = (prop) => {
            axios.post('http://79.174.92.124:9999/end_workout?user_id=' + localStorage.getItem('token') + '&identification_key=1234')

            .then(() => {toast.success('Тренировка окончена')})
            .catch(error => {
                console.log(error)
                let errMsg = error.response ? `Статус: ${error.response.status}, Данные: ${error.response.data}` // Ответ сервера содержит ошибку
                                           : error.request ? 'Нет ответа' // Ответ от сервера не получен
                                                           : `Ошибка настройки запроса: ${error.message}`; // Неправильная настройка запроса
                toast.error(errMsg); // Подробности ошибки
              })
        };


        const formSubmited = (e) => {
            console.log(e.submitter)
            e.preventDefault()
            const formData = new FormData(e.currentTarget);
            const data = Object.fromEntries(formData);
            
            this.setState({
                datavalue: data
            })
            const req = {
                "firstname": data.membName,
                "lastname": data.membSur,
                "patronymic": data.membPatr,
                "sex_id": data.membSex,
                "old": data.membOld,
                "key": data.membId,
                "identification_key": "1234",
                "user_id": localStorage.getItem('token')
            }
            console.log((req))
            axios.post('http://79.174.92.124:9999/start_workout', req)
            .then(() => {toast.success('Тренировка начата')})
            .catch(error => {
                let errMsg = error.response ? `Статус: ${error.response.status}, Данные: ${error.response.data}` // Ответ сервера содержит ошибку
                                           : error.request ? 'Нет ответа' // Ответ от сервера не получен
                                                           : `Ошибка настройки запроса: ${error.message}`; // Неправильная настройка запроса
                toast.error(errMsg); // Подробности ошибки
              });
        };

       
    
        return(<>
            <div style={{textAlign: 'center', marginTop: '20px'}}>
                <Link to='/choice' className="panel">Главная</Link>
                <Link to="/newMemb" className="panel">Новый участник</Link>
                <Link to="/ListOfMembers" className="panel">Список участников</Link>
                <Link className="panel" style={{color: 'red'}} onClick={exit}>Выйти</Link>
            </div>
            <div style={{display: 'inl'}} className="newMemb">
                <Header title="Новый участник"/>
                <form id='newmemb' onSubmit={formSubmited}>
                    <Bar type="text" for = 'membName' id='membName' title='Имя участника' name='membName'/>
                    <Bar type="text" for = 'membSur' id='membSur' title='Фамилия участника' name='membSur'/>
                    <Bar type="text" for = 'membPatr' id='membPatr' title='Отчество участника' name='membPatr'/>
                    <Bar type="text" for = 'membSex' id='membSex' title='Пол участника' name='membSex' placeholder='1-М, 2-Ж'/>
                    <Bar type="text" for = 'membOld' id='membOld' title='Возраст участника' name='membOld'/>
                    <Bar type="text" for = 'membId' id='membId' title='Id участника' name='membId'/>
                <div style={{display: "flex", justifyContent: "space-between"}}>
                    <button value="Submit" type='submit' id='11' className="mnbt">Старт</button>
                    <button className='mnbt' type="button" name='Ender' id="2" onClick={stopped}>Стоп</button>
                </div>
                </form>
               
            </div>
        </>
        )
    }

    
}