import "../Styles/global.css"
import React, { useState } from "react"

export function TextBars () {
    return (
        <div>
        <h1 style={{fontSize: "3.3em", color: "blue", fontWeight: "Bolder"}}>Авторизация</h1>
            <div>
                <label style={{fontSize: "17pt"}} for='name'>Логин</label>
                <p><input className="forms" type="text" id="name" /></p>
                <label style={{fontSize: "17pt"}} for='password'>Пароль</label>
                <p><input className="forms" type="text" id="password" minLength={8}/></p>
            </div>
        <button className="buttons">Войти</button>
        <a href="/registration">Нет аккаунта?</a>
        </div>
    )
}

export function TextBarsReg () {
    return (
        <div>
        <h1 style={{fontSize: "3.3em", color: "blue", fontWeight: "Bolder"}}>Регистрация</h1>
            <div>
                <label style={{fontSize: "17pt"}} for='name'>Логин</label>
                <p><input className="forms" type="text" id="name" /></p>
                <label style={{fontSize: "17pt"}} for='password'>Пароль</label>
                <p><input className="forms" type="text" id="password" minLength={8}/></p>
            </div>
            <a href="/"><button className="buttons">Зарегистрироваться</button ></a>
        <a href="/">Вход</a>
        </div>
    )
}


export function Bar (props) {
    return(
        <div>
        <label style={{fontSize: "17pt", fontFamily: "Arial"}} for={props.for}>{props.title}</label>
        <p><input className="forms" name={props.name} id = {props.id} type = {props.type} placeholder={props.placeholder}/></p>
        </div>
    )
}

export function Header(props) {
    return(
        <h1 style={{fontSize: "3.3em", color: "black", fontWeight: "Bolder", fontFamily: "Arial", textAlign: 'center'}}>{props.title}</h1>
    )
} 

export function Button(props, message) {
    function clicked(message){
        console.log(message)
    }
    return(
        <a href={props.link}><button className="buttons" name={props.name} type={props.type} id={props.id}>{props.title}</button ></a>
    )
} 

export function Lists (props) {
    return (
                    <div className="listes">
                        <p>Имя: {props.data.membName}</p>
                        <p>Фамилия: {props.data.membSur}</p>
                        <p>Отчество: {props.data.membPatr}</p>
                        <p>Пол: {props.data.membSex}</p>
                        <p>Возраст: {props.data.membOld}</p>
                    </div>
    )}


